:root {
  --primary-bg-color: #ffffff;
  --secondary-bg-color: #559be7;
  --primary-text-color: #000000;
  --secondary-text-color: #ffffff;
  --special-text-color: #1776d5;
  --primary-border-color: #dddddd ;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.theme-dark {
  --primary-bg-color: #23272F;
  --secondary-bg-color: #70aae8;
  --primary-text-color: #ffffff;
  --secondary-text-color: #23272F;
  --special-text-color: #70aae8;
  --primary-border-color: #dddddd;
}
.theme-dark .header__toggle-theme::before {
  mask-image: url("../images/sprite.svg#light");
}
.theme-dark .contacts__item-link::before {
  background-color: var(--primary-text-color);
}
.theme-dark .skills__item-link--html::before {
  background-image: url("../images/icons/html-alt.svg");
}
.theme-dark .skills__item-link--css::before {
  background-image: url("../images/icons/css-alt.svg");
}
.theme-dark .skills__item-link--sass::before {
  background-image: url("../images/icons/sass-alt.svg");
}
.theme-dark .skills__item-link--react::before {
  background-image: url("../images/icons/react-alt.svg");
}
.theme-dark .skills__item-link--mssql::before {
  background-image: url("../images/icons/mssql-alt.svg");
}
.theme-dark .skills__item-link--mysql::before {
  background-image: url("../images/icons/mysql-alt.svg");
}
.theme-dark .skills__item-link--maria-db::before {
  background-image: url("../images/icons/maria-db-alt.svg");
}
.theme-dark .skills__item-link--figma::before {
  background-image: url("../images/icons/figma-alt.svg");
}
.theme-dark .works__item-technology {
  color: var(--primary-text-color);
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
}

@font-face {
  font-family: "PT Sans";
  src: url("../fonts/pt-sans-regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/pt-sans-bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.page {
  height: 100%;
}
.page--nojs .header__toggle-nav {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
}
.page--nojs .header__toggle-theme {
  grid-column: 3;
}
@media (max-width: 767px) {
  .page--nojs .nav {
    margin-top: 20px;
    position: static;
    display: block;
  }
}

.page__body {
  min-height: 100%;
  margin: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  color: var(--primary-text-color);
  background-color: var(--primary-bg-color);
}

@media (min-width: 768px) {
  .page__main {
    margin-top: 10px;
  }
}

.header {
  padding: 30px 0 20px;
}

.header__container {
  display: grid;
  grid-template-columns: 1fr auto auto;
  align-items: center;
  gap: 0 20px;
}
@media (min-width: 768px) {
  .header__container {
    grid-template-columns: 1fr auto 1fr;
  }
}

.header__logo::before {
  content: "";
  display: block;
  width: 40px;
  height: 40px;
  background-color: var(--secondary-bg-color);
  mask-image: url("../images/sprite.svg#logo");
}

.header__nav {
  grid-column: span 3;
  margin: 0 -30px;
}
@media (min-width: 768px) {
  .header__nav {
    grid-column: 2;
    grid-row: 1;
    margin: 0;
  }
}

.header__toggle-theme {
  justify-self: end;
  padding: 0;
  border: none;
  outline: none;
  background: transparent;
}
.header__toggle-theme:hover {
  cursor: pointer;
  opacity: 0.8;
}
.header__toggle-theme:active {
  opacity: 0.6;
}
.header__toggle-theme::before {
  content: "";
  display: block;
  width: 40px;
  height: 40px;
  background-color: var(--primary-text-color);
  mask-image: url("../images/sprite.svg#dark");
}

.header__toggle-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  width: 32px;
  height: 26px;
  border: none;
  outline: none;
  background-color: transparent;
}
@media (min-width: 768px) {
  .header__toggle-nav {
    display: none;
  }
}
.header__toggle-nav:hover {
  cursor: pointer;
  opacity: 0.6;
}
.header__toggle-nav:active {
  cursor: pointer;
  opacity: 0.3;
}

.header__toggle-nav-checkbox:checked + .header__toggle-nav-inner {
  background-color: transparent;
}
.header__toggle-nav-checkbox:checked + .header__toggle-nav-inner::before {
  width: 38px;
  transform: rotate(45deg) translate(0, -2px);
}
.header__toggle-nav-checkbox:checked + .header__toggle-nav-inner::after {
  width: 38px;
  transform: rotate(-45deg) translate(0, 2px);
}

.header__toggle-nav-inner {
  position: relative;
  display: inline-block;
  vertical-align: bottom;
  width: 100%;
  height: 2px;
  background-color: var(--primary-text-color);
  transform-origin: left center;
  transition: background-color 0.5s ease;
}
.header__toggle-nav-inner::before, .header__toggle-nav-inner::after {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--primary-text-color);
  transform-origin: left center;
  transition: transform 0.5s ease;
}
.header__toggle-nav-inner::before {
  top: -12px;
}
.header__toggle-nav-inner::after {
  top: 12px;
}

.main__skills {
  margin-top: 40px;
}
@media (min-width: 768px) {
  .main__skills {
    margin-top: 50px;
  }
}

.main__works {
  margin-top: 40px;
}
@media (min-width: 768px) {
  .main__works {
    margin-top: 50px;
  }
}

.main__contacts {
  margin-top: 40px;
}
@media (min-width: 768px) {
  .main__contacts {
    margin-top: 50px;
  }
}

.title-h1 {
  font-size: 28px;
  text-align: center;
}

.title-h2 {
  font-size: 24px;
}

.button {
  padding: 8px 20px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 22px;
  font-weight: 700;
  color: var(--secondary-text-color);
  text-transform: uppercase;
  border: 0;
  background-color: var(--secondary-bg-color);
}
.button:hover {
  color: var(--secondary-bg-color);
  outline: 2px solid var(--secondary-bg-color);
  background-color: var(--primary-bg-color);
  cursor: pointer;
}
.button:active {
  color: var(--secondary-text-color);
  outline: none;
  background-color: var(--secondary-bg-color);
}

.container {
  margin: 0 auto;
  padding: 0 30px;
  width: 100%;
}
@media (min-width: 768px) {
  .container {
    padding: 0 40px;
  }
}
@media (min-width: 1280px) {
  .container {
    padding: 0;
    max-width: 1220px;
  }
}

.nav {
  position: relative;
  display: none;
}
@media (min-width: 768px) {
  .nav {
    display: flex;
    align-items: center;
  }
}
.nav--opened {
  display: flex;
}

.nav__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  flex-wrap: wrap;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  border-bottom: 1px solid var(--primary-border-color);
}
@media (min-width: 768px) {
  .nav__list {
    flex-direction: row;
    align-items: center;
    gap: 30px;
    border-bottom: none;
  }
}
@media (min-width: 1280px) {
  .nav__list {
    gap: 40px;
  }
}

.nav__item {
  border-top: 1px solid var(--primary-border-color);
}
@media (min-width: 768px) {
  .nav__item {
    border-top: none;
  }
}

.nav__link {
  display: block;
  padding: 20px 0;
  color: var(--primary-text-color);
  font-size: 20px;
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
}
.nav__link:hover {
  opacity: 0.8;
}
.nav__link:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .nav__link {
    font-size: 22px;
  }
}

@media (max-width: 767px) {
  .nav--opened .nav__list {
    position: absolute;
    top: 20px;
    right: 0;
    left: 0;
    z-index: 10;
    background-color: var(--primary-bg-color);
  }
}
.intro__container {
  display: grid;
  justify-content: center;
  align-items: start;
  grid-template-columns: 1fr;
  gap: 30px;
}
@media (min-width: 768px) {
  .intro__container {
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: minmax(300px, 400px);
    gap: 40px;
  }
}
@media (min-width: 1280px) {
  .intro__container {
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: 300px;
  }
}

.intro__content {
  grid-column: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
@media (min-width: 1280px) {
  .intro__content {
    grid-column: span 2;
  }
}

.intro__title {
  align-self: center;
  margin: 0 0 -4px;
  padding-bottom: 4px;
  border-bottom: 4px solid #559BE7;
}

.intro__subtitle {
  align-self: center;
  font-weight: 700;
}

.intro__description {
  margin: 0;
}

.intro__link {
  color: var(--special-text-color);
}
.intro__link:hover {
  font-weight: 700;
}

.intro__picture {
  position: relative;
  width: 100%;
  height: 300px;
  overflow: hidden;
}
@media (min-width: 768px) {
  .intro__picture {
    height: 100%;
  }
}

.intro__image {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.works__container {
  text-align: center;
}

.works__title {
  display: inline-block;
  margin: 0 0 -4px;
  padding-bottom: 4px;
  border-bottom: 4px solid #559BE7;
}

.works__list {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: 300px;
  justify-content: center;
  gap: 30px;
  margin: 30px 0 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .works__list {
    grid-template-columns: repeat(2, 1fr);
    gap: 40px;
  }
}
@media (min-width: 1280px) {
  .works__list {
    grid-template-columns: repeat(3, 1fr);
  }
}

.works__item {
  display: flex;
  justify-content: stretch;
}
.works__item--hide {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
}

.works__item-link {
  position: relative;
  display: flex;
  justify-content: center;
  gap: 10px;
  width: 100%;
  height: 100%;
  overflow: hidden;
  text-decoration: none;
  color: var(--primary-text-color);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.6);
}

.works__item-link:hover {
  opacity: 0.8;
}

.works__item-link:active {
  opacity: 0.6;
}

.works__item-content {
  position: relative;
  z-index: 2;
  align-self: end;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 10px 20px 20px;
  background-color: rgba(255, 255, 255, 0.8);
}

.works__item-picture {
  position: absolute;
  z-index: 1;
  width: 100%;
}

.works__item-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.works__item-title {
  margin: 0;
}

.works__item-description {
  margin: 10px 0 0;
}

.works__item-technologies {
  display: flex;
  gap: 8px;
  margin: 15px 0 0;
  padding: 0;
  list-style: none;
}

.works__item-technology {
  padding: 2px 6px;
  color: var(--secondary-text-color);
  border-radius: 6px;
  background-color: var(--secondary-bg-color);
}

.works__area-button {
  display: flex;
  justify-content: center;
  align-self: center;
  margin-top: 40px;
}

.skills__container {
  text-align: center;
}

.skills__title {
  display: inline-block;
  margin: 0 auto -4px;
  padding-bottom: 4px;
  border-bottom: 4px solid #559BE7;
}

.skills__list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
  margin: 30px 0 0;
  padding: 0;
  list-style: none;
}

.skills__item-link {
  display: flex;
  width: 100px;
  height: 100px;
}
.skills__item-link:hover {
  opacity: 0.8;
}
.skills__item-link:active {
  opacity: 0.6;
}
.skills__item-link::before {
  content: "";
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
}
.skills__item-link--html::before {
  background-image: url("../images/icons/html.svg");
}
.skills__item-link--css::before {
  background-image: url("../images/icons/css.svg");
}
.skills__item-link--sass::before {
  background-image: url("../images/icons/sass.svg");
}
.skills__item-link--less::before {
  background-image: url("../images/icons/less.svg");
}
.skills__item-link--js::before {
  background-image: url("../images/icons/js.svg");
}
.skills__item-link--ts::before {
  background-image: url("../images/icons/ts.svg");
}
.skills__item-link--react::before {
  background-image: url("../images/icons/react.svg");
}
.skills__item-link--php::before {
  background-image: url("../images/icons/php.svg");
}
.skills__item-link--mssql::before {
  background-image: url("../images/icons/mssql.svg");
}
.skills__item-link--mysql::before {
  background-image: url("../images/icons/mysql.svg");
}
.skills__item-link--maria-db::before {
  background-image: url("../images/icons/maria-db.svg");
}
.skills__item-link--figma::before {
  background-image: url("../images/icons/figma.svg");
}

.contacts {
  padding: 0 0 20px;
}
@media (min-width: 768px) {
  .contacts {
    padding-bottom: 30px;
  }
}
@media (min-width: 1280px) {
  .contacts {
    padding-bottom: 40px;
  }
}

.contacts__container {
  text-align: center;
}

.contacts__title {
  display: inline-block;
  margin: 0 0 -4px;
  padding-bottom: 4px;
  border-bottom: 4px solid #559BE7;
}

.contacts__list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
  margin: 20px 0 0;
  padding: 0;
  list-style: none;
}

.contacts__item-link {
  display: flex;
}
.contacts__item-link:hover {
  opacity: 0.8;
}
.contacts__item-link:active {
  opacity: 0.6;
}
.contacts__item-link::before {
  content: "";
  width: 50px;
  height: 50px;
}
.contacts__item-link--mail::before {
  background-color: #DD2626;
  mask-image: url("../images/sprite.svg#mail");
}
.contacts__item-link--telegram::before {
  background-color: #2AABEE;
  mask-image: url("../images/sprite.svg#telegram");
}
.contacts__item-link--github::before {
  background-color: #24292F;
  mask-image: url("../images/sprite.svg#github");
}